import { Component, OnInit, AfterViewInit } from '@angular/core';
import { Http } from '@angular/http';
import { OnChanges } from '@angular/core';
import { Observable } from 'rxjs/Rx';
// import { interval } from 'rxjs/Observable/interval';
import {Router, ActivatedRoute, NavigationEnd} from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { MasonryOptions } from 'angular2-masonry';
 

const scr = 'http://ppql.ru/masonry.js';
interface Category {
  id: number;
  name: string;
}

export class NewsRaw {
  id: number;
  title: string;
  desc: string;
  main: number;
  category:string;
  photo: string;
  created_at:string;
  workplace: string;
  name_credits: string;
  comments_count: string;
}
@Component({
  selector: 'app-all-interview',
  templateUrl: './all-interview.component.html',
  styleUrls: ['./all-interview.component.scss']
})
export class AllInterviewComponent implements OnInit {

public myOptions: MasonryOptions = { 
      transitionDuration: '0' 
};
  news_raw: any[];
	news: NewsRaw[] = [];
  main_news: NewsRaw[] = [];
  allCount = 0;
   constructor(private http:HttpClient, private router:Router, private route:ActivatedRoute) { 
   	let path = "/interviewraw";
   	const info = http.get(path);
   	info.subscribe(response => {
       // console.log(response['news']);
       // this.news = response['news'];
       for (let item of response['news']) {
         console.log(item.photos[0].file);
           this.news.push( {
         id: item.id,
          title: item.title,
          desc: item.desc,
          main: item.main,
          created_at:item.created_at,
          category: item.category.name,
          photo: item.photos[0].file,
          name_credits:item.name_credits,
          workplace:item.workplace,
          comments_count:item.comments_count

       });
         }
         

       for (let item of response['main_news']) {
         console.log(item);
         this.main_news.push( {
         id: item.id,
          title: item.title,
          desc: item.desc,
          main: item.main,
          category:item.category.name,
          created_at:item.created_at,
          photo: item.photos[0].file,
          name_credits:item.name_credits,
          workplace:item.workplace,
          comments_count:item.comments_count

       });
       }
   		// this.news.push(response['news']);
     //   this.main_news.push(response['main_news']);
   		// console.log(response['news']);
       console.log(this.news);
       console.log(this.news[0].photo);
       console.log(this.main_news);
       this.allCount = this.main_news.length+this.news.length;
   	});



   }

  ngOnInit() {
    
    
  }

  loadMore(id) {
    this.router.navigate(['/interview/item', id]);
  }
  

}



